.contacts {
  background-color: var(--c-white-default);

  &--inner {
    background-color: var(--c-basic-gray);

    @media (min-width: $desktop-width) {
      background-color: var(--c-white-default);
    }
  }
}

.contacts__wrapper {
  position: relative;
  margin: 0 auto;
}

.contacts__container {
  padding: 26px 20px;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    padding: 56px 70px 62px;
  }

  @media (min-width: $desktop-width) {
    width: 565px;
    position: absolute;
    background-color: var(--c-white-default);
    top: 95px;
    left: 110px;
    gap: 87px;
    align-items: center;
  }
}

.contacts__lead {
  margin: 0;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--c-black);
  width: 139px;

  @media (min-width: $tablet-width) {
    width: 172px;
    font-size: 20px;
    line-height: 26px;
  }

  @media (min-width: $desktop-width) {
    width: 163px;
  }
}

.contacts__address {
  width: 139px;

  @media (min-width: $tablet-width) {
    width: 160px;
    text-align: right;
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 175px;
    text-align: start;
  }

  p {
    margin: 0;

    @media (min-width: $tablet-width) {
      margin-bottom: 19px;
    }
  }
}

.contacts__address,
.contacts__city {
  margin: 0;
  line-height: 20px;
}

.contacts__map {
  height: 362px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    z-index: -1;
  }

  @media (min-width: $tablet-width) {
    height: 400px;
  }
}

.contacts iframe {
  border: none;
  display: block;

  @media (min-width: $tablet-width) {
    height: 400px;
  }
}
