:root {
  --c-dark-gray: #444444;
  --c-black: #000000;
  --c-secondary-black: #222222;
  --c-basic-green: #68b738;
  --c-light-gray: #eaeaea;
  --c-basic-gray: #f2f2f2;
  --c-special-gray: #cdcdcd;
  --c-pressed-green: #5eaa2f;
  --c-error-red: #ff8282;
  --c-white-default: #ffffff;
  --main-font: "Lato", "Arial", sans-serif;
  --accent-font: "Oswald", "Arial", sans-serif;
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  height: 100%;
}

body {
  min-width: 320px;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 18px;
  color: var(--c-dark-gray);
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  width: 100%;
  background-color: var(--c-white-default);
  color: var(--c-black);
}
