.main-nav {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  padding: 0;
  top: -65px;
  right: 0;
  z-index: 1;
  display: block;
  width: 64px;
  height: 65px;
  background-color: var(--c-white-default);
  border: none;
  cursor: pointer;
}
.main-nav__toggle:active {
  background-color: var(--c-basic-gray);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 1px;
  background-color: var(--c-black);
  box-shadow: 0 8px 0 0 var(--c-black), 0 16px 0 0 var(--c-black);
  content: "";
  z-index: 2;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 20px;
  width: 24px;
  height: 1px;
  background-color: var(--c-black);
  z-index: 2;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: var(--c-basic-gray);
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .main-nav--closed .user-list__item {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
