.catalog-additional {
  display: flex;
  flex-direction: column;
  gap: 35px;
  padding: 25px 0 35px;
  margin: 0 auto;
  width: 320px;
  background-color: var(--c-white-default);

  @media (min-width: $tablet-width) {
    padding: 65px 30px 68px;
    width: 768px;
    gap: 69px;
  }

  @media (min-width: $desktop-width) {
    padding: 38px 110px 75px;
    width: 1440px;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 80px;
  }
}

.catalog-additional__title {
  position: relative;
  margin: 0;
  margin-bottom: 1px;
  margin-left: 20px;
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--c-black);

  @media (min-width: $tablet-width) {
    margin-bottom: 11px;
    margin-left: 0;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 1px;
    width: 100%;

    &::before {
      position: absolute;
      bottom: -81px;
      left: 0;
      right: 0;
      width: 895px;
      height: 1px;
      background-color: var(--c-special-gray);
      content: "";
    }
  }
}

.catalog-additional__list {
  padding: 0;
  margin: 0 auto;
  width: 280px;
  list-style-type: none;

  @media (min-width: $tablet-width) {
    width: 708px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    width: 895px;
  }
}

.catalog-additional__item {
  padding: 13px 0 19px;
  display: flex;
  flex-direction: column;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 26px 38px 28px;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0;
    box-shadow: inset 0 0 0 1px var(--c-special-gray);
  }

  @media (min-width: $desktop-width) {
    box-shadow: none;
    padding: 15px 0 17px;
    align-items: center;
    flex-wrap: nowrap;

    &::before {
      position: absolute;
      margin: 0 auto;
      bottom: 0;
      left: 0;
      right: 0;
      width: 895px;
      height: 1px;
      background-color: var(--c-special-gray);
      content: "";
    }
  }
}

.catalog-additional__heading {
  margin: 0;
  margin-bottom: 13px;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--c-secondary-black);

  @media (min-width: $tablet-width) {
    margin-right: auto;
    margin-bottom: 25px;
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    width: 300px;
  }
}

.catalog-additional__description-list {
  margin: 0;
  margin-bottom: 15px;
  line-height: 14px;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    margin-right: 40px;
    width: 285px;
  }
}

.catalog-additional__desc-definition {
  justify-content: end;
  margin: 0;

  @media (min-width: $tablet-width) {
    margin-left: 49px;
  }
}

.catalog-additional__order-link {
  padding: 10px 20px;
  width: 100%;
  font-size: 16px;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    flex-grow: 1;
  }

  @media (min-width: $desktop-width) {
    width: 244px;
    flex-grow: 0;
  }
}

.catalog-additional__wrapper {
  position: relative;
  padding: 187px 61px 43px 58px;
  background-color: var(--c-basic-green);
  background-image: url("../img/image-additional-product.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 0;

  @media (min-width: $tablet-width) {
    padding: 71px 124px 69px 80px;
    background-position: 100% 60%;
    background-size: cover;
    display: flex;
    justify-content: end;
  }

  @media (min-width: $desktop-width) {
    padding: 185px 43px 42px 40px;
  }

  &::before {
    position: absolute;
    z-index: -1;
    margin: 0 auto;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(104, 183, 56, 0.85);
    background-image: url("../img/icon_gift.svg");
    background-repeat: no-repeat;
    background-position: 101px 59px;
    background-size: 78px 80px;
    content: "";

    @media (min-width: $tablet-width) {
      background-position: 77px 57px;
    }

    @media (min-width: $desktop-width) {
      background-position: 84px 56px;
    }
  }
}

.catalog-additional__bonus {
  padding: 20px;
  font-size: 16px;
  line-height: 20px;
  color: var(--c-white-default);

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.catalog-additional__text {
  margin: 0;
  width: 161px;
  text-align: center;

  @media (min-width: $tablet-width) {
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    text-align: center;
  }
}
